@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-blue: #0075ff;
  --primary-green: #22c55e;
  --primary-orange: #f59e0b;
  --primary-red: #ff0000;
  --primary-gray: #6b7290;
  --secondary-gray: #dddddd;
}
body {
  font-family: "Open Sans", sans-serif;
}

@layer utilities {
  .tipTop {
    position: absolute;
    top: -32px;
    right: -14px;
    width: 32px;
    padding: 2px 0;
    font-size: 10px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
  }
  .tipTopAfter {
    position: absolute;
    top: -16px;
    right: -5px;
    width: 0;
    border-width: 7px;
    border-left-color: transparent;
    border-right-color: transparent;
    border-bottom-color: transparent;
  }
}
.active {
  background-color: rgb(243 244 246);
}
